@include tablet{
    .contenedor-anuncios{
        @include grid(3, 2rem) //3 columnas con separacionn de 2 rem que es igual a 20px
    }
}

.anuncio{
    background-color: lighten($gris, 5);
    border: 0.5px solid lighten($grisOscuro, 10);

    //---- EFECTOS PRODUCTOS QUIE YO COLOQUE
    //border-radius: $separacion; 
    //display: inline-flex;
    display: inline-block;
    overflow: hidden;
    //---- EFECTOS PRODUCTOS QUE YO COLOQUE

    .contenido-anuncio{
        padding: 2rem;
    }

    h3, p{
        margin: 0;
    }

    .precio{
        color: $verde;
        font-weight: $bold;
        font-size: 3rem;
    }
}

.iconos-caracteristicas{
    display: flex;
    list-style: none;
    padding: 0;

    li{
        flex: 1;
        display: flex;

        img{
            flex: 0 0 5rem;
            margin-right: 1rem;
        }
        p{
            font-weight: $black;
            
        }
    }
}